import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import axios from "../axios";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import {
  loginUser,
  init_fav,
  init_login_user,
  init_user_token,
  init_card
} from "../actions/actions";
import {
  post_req,
  post_load_success,
  post_load_failed
} from "../actions/post";

import { existing_address } from "../actions/costumerInfo";

import * as EmailValidator from "email-validator";
import Header from "./header";
import Home from "./home";
import Spinner from "./details/spinner";
class Login extends Component {
  constructor() {
    super();
    this.state = {
      username: "",
      password: "",
      redirectState: false,
      error: "",
      spinner: "none"
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onSubmit(event) {
    this.props.post_req();
    event.preventDefault();
    axios
      .post("/auth/jwt/login/", {
        username: this.state.username,
        password: this.state.password
      })
      .then(res => {
        console.log("login", res);

        if (res.data.status < 400) {
          localStorage.setItem("jwtToken", JSON.stringify(res.data.data));
          this.props.init_login_user(res.data.data);
          this.props.init_user_token(res.data.data.token);
          this.props.init_fav();
          this.props.existing_address();
          this.props.init_card();
          this.props.post_load_success();
          this.setState({
            redirectState: true
          });
          alert("خوش آمدید");
        } else {
          this.props.post_load_failed(res.data.errors);
        }
      })
      .catch(error => {
        this.props.post_load_failed(error.response.data.errors);
        // console.log(error, "login error with catch");
      });

    // this.props.loginUser( this.state.username , this.state.password);
  }

  onChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    });
  }

  render() {
    // console.log(this.props.loginToken ,'usertoken')
    const { redirectState } = this.state;
    return (
      <>
        <Header />

        <div className="login-box">
          <p>ورود به فروشگاه</p>
          <form
            onSubmit={e => {
              this.onSubmit(e);
            }}
          >
            <label>ایمیل</label>
            <input
              type="text"
              placeholder="نام کاربری یا ایمیل خور را وارد نمایید"
              value={this.state.username}
              onChange={this.onChange}
              name="username"
              required
            />

            <label>رمز عبور</label>
            <input
              type="password"
              placeholder="رمز عبور را وارد نمایید"
              value={this.state.password}
              onChange={this.onChange}
              name="password"
              required
            />
            <a href="#">رمز عبور خود را فراموش کرده اید ؟</a>
            <span className="login-error">
              {this.state.error ? this.state.error.map(e => <p>{e}</p>) : null}
            </span>
            <Spinner display={this.state.spinner} />
            <button type="submit">ورود </button>
          </form>
          <div>
            کاربر جدید هستید ؟<Link to={`/user/sign-up`}>ثبت نام کنید </Link>
            <p>
              <Link to={"/"}>صفحه اصلی</Link>
            </p>
          </div>
          {redirectState && <Redirect to={`/`} />}
        </div>
      </>
    );
  }
}
const mapStateToProps = state => {
  return {
    currentUser: state.InitUserReducer.currentUser,
    loginToken: state.InitUserReducer.userToken
  };
};

const mapDispatchToProps = {
  init_login_user: init_login_user,
  setFavourite: init_fav,
  init_user_token: init_user_token,
  init_fav: init_fav,
  existing_address: existing_address,
  init_card: init_card,
  post_req: post_req,
  post_load_failed: post_load_failed,
  post_load_success: post_load_success
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
